import { GetToken } from "@/utilities/cookies";
import MyDownloads from "@/Components/MyDownloads";
import createMetadata from "@/Components/Seo/createMetadata";

export async function generateMetadata() {
  const title = "My Downloads";

  return createMetadata({
    title: title,
  });
}

const Page = async () => {
  const token = await GetToken();

  return <MyDownloads token={token} />;
};

export default Page;
